@use "base/mixins" as m;
@use "base/variables" as v;

.formulario {   //form
    display: flex;
    flex-direction: column;

    &__fieldset {
        border: none;
        margin-top: 1rem;
        padding: 0;
    }

    &__legend {
        color: v.$gris;
        font-size: 2.2rem;
        margin-bottom: 2rem;
    }

    &__campo {  //div
        display: flex;
        flex-direction: column;
        gap: 1rem;
        margin: 1.5rem 0;
    }
    &__contenedor-icono {   //div
        display: flex;
        border-radius: .5rem;
        overflow: hidden;   //si no le pongo esto no se aplica el radius
    }
    &__icono {  //div
        background-color: v.$gris-oscuro;
        width: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;
        i {
            color: v.$blanco;
            font-size: 2rem;
        }
    }
    &__label {
        display: block;
        font-size: 1.8rem;
        color: rgba(v.$negro, .8);
    }
    &__input,
    &__select {
        padding: 1rem;
        border: 1px solid darken(v.$gris-claro, 20);
        border-radius: .5rem;

        &--file {
            border: none;
        }
        &--sociales {
            @extend .formulario__input;
            flex: 1;    //que tome todo el ancho que no ocupa el div de icono
            border-top-left-radius: 0;  //quito los bordes curvos izquierdos al input
            border-bottom-left-radius: 0;
        }
    }
    &__radio {
        display: flex;
        gap: 2rem;
    }
    &__texto {
        margin-top: 3rem;
    }  
    &__imagen {
        max-width: 20rem;
    }
    &__listado {    //div
        @include m.tags;
    }
    &__submit {
        @include m.boton(v.$azul, v.$blanco);
        width: auto;
        align-self: flex-start;

        &--registrar {
            width: 100%;
        }
    }

}
